import React, { useState } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Box,
  Button,
  Card,
  Checkbox,
  Divider,
  IconButton,
  InputAdornment,
  SvgIcon,
  Tab,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  Tabs,
  TextField,
  makeStyles
} from '@material-ui/core';
import {
  Edit as EditIcon,
  ArrowRight as ArrowRightIcon,
  Search as SearchIcon
} from 'react-feather';
import GetAppIcon from '@material-ui/icons/GetApp';
import FormatListNumberedIcon from '@material-ui/icons/FormatListNumbered';
import getInitials from 'src/utils/getInitials';
import {deletegroups} from '../../../api/groups.js';
const tabs = [
  {
    value: 'all',
    label: 'Tất cả'
  }
];

const sortOptions = [
  {
    value: 'updatedAt|desc',
    label: 'Last update (newest first)'
  },
  {
    value: 'updatedAt|asc',
    label: 'Last update (oldest first)'
  },
  {
    value: 'name|desc',
    label: 'Tên (giảm)'
  },
  {
    value: 'name|asc',
    label: 'Tên (tăng)'
  }
];

const applyFilters = (groups, query, filters) => {
  return groups.filter((group) => {
    let matches = true;

    if (query) {
      const properties = ['title', 'description'];
      let containsQuery = false;

      properties.forEach((property) => {
        if ((property in group) && group[property].toLowerCase().includes(query.toLowerCase())) {
          containsQuery = true;
        }
      });

      if (!containsQuery) {
        matches = false;
      }
    }

    Object.keys(filters).forEach((key) => {
      const value = filters[key];

      if (value && group[key] !== value) {
        matches = false;
      }
    });

    return matches;
  });
};

const applyPagination = (groups, page, limit) => {
  return groups.slice(page * limit, page * limit + limit);
};

const descendingComparator = (a, b, orderBy) => {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }

  if (b[orderBy] > a[orderBy]) {
    return 1;
  }

  return 0;
};

const getComparator = (order, orderBy) => {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
};

const applySort = (groups, sort) => {
  const [orderBy, order] = sort.split('|');
  const comparator = getComparator(order, orderBy);
  const stabilizedThis = groups.map((el, index) => [el, index]);

  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);

    if (order !== 0) return order;

    return a[1] - b[1];
  });

  return stabilizedThis.map((el) => el[0]);
};

const useStyles = makeStyles((theme) => ({
  root: {},
  queryField: {
    width: 500
  },
  bulkOperations: {
    position: 'relative'
  },
  bulkActions: {
    paddingLeft: 4,
    paddingRight: 4,
    marginTop: 6,
    position: 'absolute',
    width: '100%',
    zIndex: 2,
    backgroundColor: theme.palette.background.default
  },
  bulkAction: {
    marginLeft: theme.spacing(2)
  },
  avatar: {
    height: 42,
    width: 42,
    marginRight: theme.spacing(1)
  }
}));

const Results = ({ groups, ...rest }) => {

  const classes = useStyles();
  const [currentTab, setCurrentTab] = useState('all');
  const [selectedgroups, setSelectedgroups] = useState([]);
  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(10);
  const [query, setQuery] = useState('');
  const [sort, setSort] = useState(sortOptions[0].value);
  const [filters, setFilters] = useState({
    hasAcceptedMarketing: null,
    isProspect: null,
    isReturning: null
  });

  const handleTabsChange = (event, value) => {
    const updatedFilters = {
      ...filters,
      hasAcceptedMarketing: null,
      isProspect: null,
      isReturning: null
    };

    if (value !== 'all') {
      updatedFilters[value] = true;
    }

    setFilters(updatedFilters);
    setSelectedgroups([]);
    setCurrentTab(value);
  };

  const handleQueryChange = (event) => {
    event.persist();
    setQuery(event.target.value);
  };

  const handleSortChange = (event) => {
    event.persist();
    setSort(event.target.value);
  };

  const handleSelectAllgroups = (event) => {
    setSelectedgroups(event.target.checked
      ? groups.map((group) => group.id)
      : []);
  };

  const handleSelectOnegroup = (event, groupId) => {
    if (!selectedgroups.includes(groupId)) {
      setSelectedgroups((prevSelected) => [...prevSelected, groupId]);
    } else {
      setSelectedgroups((prevSelected) => prevSelected.filter((id) => id !== groupId));
    }
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  const handleLimitChange = (event) => {
    setLimit(parseInt(event.target.value));
  };

  const handleDelete = () => {
      deletegroups(selectedgroups);
    };

  const filteredgroups = applyFilters(groups, query, filters);
  const sortedgroups = applySort(filteredgroups, sort);
  const paginatedgroups = applyPagination(sortedgroups, page, limit);
  const enableBulkOperations = selectedgroups.length > 0;
  const selectedSomegroups = selectedgroups.length > 0 && selectedgroups.length < groups.length;
  const selectedAllgroups = selectedgroups.length === groups.length;

  return (
    <Card className={clsx(classes.root)}{...rest}>
      <Tabs onChange={handleTabsChange} scrollButtons="auto" textColor="secondary" value={currentTab} variant="scrollable">
        {tabs.map((tab) => (
          <Tab key={tab.value} value={tab.value} label={tab.label}/>
        ))}
      </Tabs>
      <Divider />
      <Box p={2} minHeight={56} display="flex" alignItems="center">
        <TextField
          className={classes.queryField}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SvgIcon fontSize="small" color="action">
                  <SearchIcon />
                </SvgIcon>
              </InputAdornment>
            )
          }}
          onChange={handleQueryChange}
          placeholder="Tìm kiếm"
          value={query}
          variant="outlined"
        />
        <Box flexGrow={1} />
        <TextField label="Sắp xếp" name="sort" onChange={handleSortChange} select SelectProps={{ native: true }} value={sort} variant="outlined">
          {sortOptions.map((option) => (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          ))}
        </TextField>
      </Box>
      {enableBulkOperations && (
        <div className={classes.bulkOperations}>
          <div className={classes.bulkActions}>
            <Checkbox checked={selectedAllgroups} indeterminate={selectedSomegroups} onChange={handleSelectAllgroups}/>
            <Button variant="outlined" className={classes.bulkAction} onClick={handleDelete}>
              Xóa
            </Button>
          </div>
        </div>
      )}
      <PerfectScrollbar>
        <Box minWidth={700}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell padding="checkbox">
                  <Checkbox checked={selectedAllgroups} indeterminate={selectedSomegroups} onChange={handleSelectAllgroups}/>
                </TableCell>
                <TableCell>
                  ID
                </TableCell>
                <TableCell>
                  Tên nhóm
                </TableCell>
                <TableCell>
                  Mã nhóm
                </TableCell>
                <TableCell align="right">
                  Actions
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {paginatedgroups.map((group) => {
                let upvotes = null;
                let downvotes = null;
                if("reactions" in group){
                  upvotes = group.reactions.upvotes
                  downvotes = group.reactions.downvotes
                }

                const isgroupSelected = selectedgroups.includes(group.id);

                if(!("roles" in group)){
                  group.roles=[""];
                }
                if(!("tags" in group)){
                  group.tags=[""];
                }
                return (
                  <TableRow hover key={group.id} selected={isgroupSelected}>
                    <TableCell padding="checkbox">
                      <Checkbox checked={isgroupSelected} onChange={(event) => handleSelectOnegroup(event, group.id)} value={isgroupSelected}/>
                    </TableCell>
                    <TableCell>
                      <Box display="flex" alignItems="center">{group.id}</Box>
                    </TableCell>
                    <TableCell>
                      {group.name}
                    </TableCell>
                    <TableCell>
                      {group.hash_id}
                    </TableCell>
                    <TableCell align="right">
                      <IconButton component={RouterLink} to={"/teacher/management/groups/edit/"+ group.id}>
                        <SvgIcon fontSize="small">
                          <EditIcon />
                        </SvgIcon>
                      </IconButton>
                      <IconButton component={RouterLink} to={"/teacher/management/groups/view_member/"+ group.id}>
                        <SvgIcon fontSize="small">
                          <FormatListNumberedIcon />
                        </SvgIcon>
                      </IconButton>
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </Box>
      </PerfectScrollbar>
      <TablePagination
        component="div"
        count={filteredgroups.length}
        onChangePage={handlePageChange}
        onChangeRowsPerPage={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
    </Card>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  groups: PropTypes.array.isRequired
};

Results.defaultProps = {
  groups: []
};

export default Results;
